@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700&display=swap);
body {
  margin: 0;
  font-family: 'Roboto', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.logo,
.logo2 {
  height: 10vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .logo {
    -webkit-animation: logo-spin-cw infinite 20s linear;
            animation: logo-spin-cw infinite 20s linear;
  }
  .logo2 {
    -webkit-animation: logo-spin-acw infinite 20s linear;
            animation: logo-spin-acw infinite 20s linear;
  }
}

.landing-header {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  padding-left: 3rem;
  font-size: calc(10px + 2vmin);
  color: whitesmoke;
  background-image: url(/hein/static/media/codebg.61f9e79d.jpg);
  background-size: 85pc 100vh;
  background-repeat: no-repeat;
  position: relative;
}

.sub1 {
  margin-top: -1rem;
  color: #7e7e7e;
}

.sub2 {
  margin-top: 5rem;
  margin-bottom: 1rem;
  color: #7e7e7e;
}

.sub3 {
  margin-top: 1rem;
  color: #7e7e7e;
}

@-webkit-keyframes logo-spin-cw {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes logo-spin-cw {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@-webkit-keyframes logo-spin-acw {
  from {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
  to {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
}
@keyframes logo-spin-acw {
  from {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
  to {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
}

.page {
    height: 100vh;
    color: whitesmoke;
    margin-top: 4rem;
}

a {
    text-decoration: none;
    color: rgb(79, 141, 141);
}

.chips {
    margin: 0.3em;
}
.contact-page {
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 100vh;
    color: whitesmoke;
    margin-top: 4rem;
}
